@import url("https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap");
:root {
  --bb-nav: #1E1E1E;
  --bb-pink: #F02666;
  --bb-pink-light: #EA45A0;
  --bb-green: #32806F;
  --bb-green-light: #00AD45;
  --bb-fluo: #64FF4F;
  --bb-blue: #3B86E9;
  --bb-dark-blue: #3F4C5E;
  --bb-salmon: #FF8A8A;
  --bb-purple: #8955FC;
  --bb-purple-light: #8850FE;
  --bb-grey: #202225;
  --bb-red: #ED2D45;
  --bb-white: #FFFFFF;
  --bb-black: #000000;
}

@font-face {
  font-family: "Dogica";
  src: url(../../fonts/OTF/dogica.otf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "VHSGothic";
  src: url(../../fonts/VHSGhotic/vhs-gothic.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Visitor";
  src: url(../../fonts/Visitor/visitor1.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Retro-Computer";
  src: url(../../fonts/TTF/retro_computer.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.share-tech-mono-regular {
  font-family: "Share Tech Mono", monospace;
  font-weight: 400;
  font-style: normal;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Retro-Computer", sans-serif;
}

body {
  position: relative;
  background: var(--bb-nav);
}

nav {
  position: fixed;
  width: 100%;
  height: 100px;
  z-index: 10;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  height: 100%;
}

.nav-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.nav-menu a,
.nav-menu button,
.nav-menu li {
  cursor: pointer;
}
.nav-menu__mobile {
  display: none;
}
.nav-menu__logo, .nav-menu__others {
  width: 20%;
}
.nav-menu__links {
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  height: 100%;
  text-transform: uppercase;
  font-size: 1.4rem;
  color: var(--bb-white);
}
.nav-menu__link {
  padding: 1rem 2rem;
  background: var(--bb-grey);
  border-radius: 0.5rem;
  transition: background 0.4s;
}
.nav-menu__link:hover {
  background: var(--bb-green);
}
.nav-menu__others {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-menu__others-link {
  padding: 2rem 2.6rem;
  background: var(--bb-purple);
  color: var(--bb-white);
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 600;
  transition: background 0.4s;
}
.nav-menu__others-link:hover {
  background: var(--bb-white);
  color: var(--bb-nav);
}

header video {
  max-height: 100vh;
  object-fit: cover;
  overflow-clip-margin: content-box;
  overflow: clip;
}

.tickets {
  background: url(../img/ticketsBg.png);
  background-size: cover;
  background-position: center;
  height: 100vh;
  font-family: "Retro-Computer", sans-serif;
}
.tickets__container {
  padding: 3rem 0 0;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  height: 100%;
}
.tickets__container h2 {
  font-size: 3.2rem;
  text-transform: uppercase;
  color: var(--bb-white);
}
.tickets__box {
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  max-width: 1480px;
  width: 100%;
}
.tickets__ticket {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tickets__ticket img {
  width: 100%;
  height: auto;
  border-radius: 1.8rem;
  box-shadow: 0px 0px 15px 2px #696969;
}
.tickets__ticket button {
  margin: 2rem auto 0;
  padding: 1rem 2.4rem;
  font-size: 2.5rem;
  font-family: "Retro-Computer", sans-serif;
  border-radius: 2rem;
  border: none;
  color: var(--bb-white);
  box-shadow: 0 0 10px 0px #696969;
  transition: background 0.4s, color 0.4s;
  cursor: pointer;
}
.tickets__ticket:nth-of-type(1) button {
  background: var(--bb-purple-light);
}
.tickets__ticket:nth-of-type(2) button {
  background: var(--bb-green-light);
}
.tickets__ticket:nth-of-type(3) button {
  background: var(--bb-pink-light);
}
.tickets__ticket button:hover {
  background: var(--bb-white);
  color: var(--bb-black);
}

.line-up {
  position: relative;
  height: 100vh;
  background: url(../img/lineUpBg.png);
  background-size: cover;
  background-position: center;
}
.line-up__box {
  margin: 0 auto;
  padding: 10rem 5rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1600px;
  height: 100%;
  border-radius: 1.6rem;
}
.line-up__localization-box {
  padding: 2rem 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
}
.line-up__localization-box h2 {
  padding: 2.2rem 5rem;
  font-size: 3.2rem;
  text-transform: uppercase;
  color: var(--bb-white);
  border: 1px solid var(--bb-salmon);
  border-radius: 1.2rem;
}
.line-up__header {
  display: flex;
}
.line-up__title {
  padding: 2rem 0;
  color: var(--bb-red);
  font-size: 2.5rem;
  text-transform: uppercase;
  width: 100%;
}
.line-up__btns {
  display: flex;
  gap: 2rem;
}
.line-up__btns button {
  padding: 2rem 2.4rem;
  color: var(--bb-white);
  border: 1px solid var(--bb-white);
  border-radius: 1rem;
  font-size: 1.9rem;
  font-weight: 900;
  text-transform: uppercase;
  transition: background 0.4s, color 0.4s;
  cursor: pointer;
}
.line-up__btns button:nth-of-type(1) {
  background: var(--bb-purple);
}
.line-up__btns button:nth-of-type(1):hover {
  background: var(--bb-white);
  color: var(--bb-purple);
}
.line-up__btns button:nth-of-type(2) {
  background: var(--bb-red);
}
.line-up__btns button:nth-of-type(2):hover {
  background: var(--bb-white);
  color: var(--bb-red);
}
.line-up__artist-box {
  padding: 2rem 0;
  display: flex;
  align-items: center;
  align-self: center;
  height: 50%;
}
.line-up__artist-box span {
  color: var(--bb-white);
  font-size: 3.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.line-up__footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 30%;
}
.line-up__bbtv {
  font-size: 3rem;
}
.line-up__yt {
  max-width: 220px;
}
.line-up__yt img {
  width: 100%;
  height: auto;
}

.media {
  margin: 4rem 2rem;
  position: relative;
}
.media__box {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1600px;
}
.media__box-top, .media__box-bottom {
  display: flex;
  border-radius: 1.6rem;
  aspect-ratio: 3/1;
}
.media__box-top {
  flex-direction: row;
  gap: 2rem;
}
.media__box-left, .media__box-right {
  padding: 2rem 5rem;
  width: 50%;
  border-radius: 2rem;
}
.media__box-left {
  background: url(../img/mediaBoxLeft.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.media__box-left h3 {
  font-size: 2rem;
  text-transform: uppercase;
  color: var(--bb-white);
}
.media__box-right {
  display: flex;
  justify-content: center;
  align-items: end;
  background: url(../img/mediaBoxRight.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.media__box-right span {
  font-size: 2rem;
  text-transform: uppercase;
  color: var(--bb-white);
}
.media__box-bottom {
  margin: 5rem 0 0;
  max-height: 589px;
  background: url(../img/mediaBoxSocials.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.media__box-social-left {
  padding: 4rem 0 0 5rem;
}
.media__box-social-left h3 {
  font-size: 2rem;
  text-transform: uppercase;
  color: var(--bb-white);
}

@media screen and (max-width: 992px) {
  .nav-menu {
    justify-content: flex-end;
  }
  .nav-menu__mobile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav-menu__mobile button {
    color: var(--bb-white);
    background-color: transparent;
    border: none;
    width: 40px;
    cursor: pointer;
  }
  .nav-menu__logo, .nav-menu__mobile, .nav-menu__others {
    width: 30%;
  }
  .nav-menu__links {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100vw;
    height: 100vh;
    background: white;
  }
}
@media screen and (max-width: 769px) {
  .media__box-top, .media__box-bottom {
    flex-direction: column;
    aspect-ratio: 2/3;
    height: 100%;
  }
  .media__box-left, .media__box-right, .media__box-social-left {
    padding: 2rem;
    width: 100%;
    height: 100%;
  }
}
@media screen and (max-width: 600px) {
  .localization__box h2 {
    padding: 1rem 2.2rem;
    font-size: 1.2rem;
    text-align: center;
    line-height: 1.8;
  }
  .line-up__box {
    padding: 2rem;
  }
  .line-up__btns button {
    padding: 1.2rem 2rem;
  }
  .line-up__artist-box span {
    font-size: 1.2rem;
    text-align: center;
    line-height: 2;
  }
}
@media screen and (max-width: 565px) {
  nav {
    height: 60px;
  }
  .nav-menu__mobile button {
    width: 30px;
  }
  .nav-menu__others-link {
    padding: 1rem 1.2rem;
    font-size: 1.2rem;
  }
  .tickets {
    padding: 2rem 0 4rem;
    height: auto;
  }
  .tickets__container h2 {
    font-size: 2.3rem;
  }
  .tickets__box {
    flex-direction: column;
    gap: 4rem;
  }
  .tickets__ticket img {
    max-width: 280px;
  }
}